import React from 'react';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton';
import classes from '../styles/projects.module.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import SvgIcon from '@material-ui/core/SvgIcon'
import {Code, FeaturedVideo, Description} from '@material-ui/icons'




export default function() {


    return (
        <div className = {classes.container}>
            <h1>Projects</h1>
        <Card className={classes.card}>
      <CardContent>
        <Typography className={classes.theme} color="textSecondary" gutterBottom>
          Full-stack developer
        </Typography>
        <Typography className={classes.title} variant="h5" component="h2">
          Como vota, deputado?
        </Typography>
        <div className = {classes.descriptionContainer}>
        <Typography className={classes.description} variant="body2" component="p">
          An open-source website for citizens to follow up congressmen after elections, and 
          see what they're voting for. I'm using React.js, Node.js and MongoDB.
          This project is currently under development.
        </Typography>
        </div>
      </CardContent>
      <CardActions>
      
        

        <a href = "https://github.com/felipeangelimvieira/comovotadeputado"> 
        <IconButton className={classes.iconButton} aria-label="Add to favorites">
          <Code/>
        </IconButton>
        </a>
        
        <a href = "https://comovota.herokuapp.com/">
        <Button size="small">Learn More</Button>
        </a>
        
      </CardActions>
    </Card>

    <Card className={classes.card}>
      <CardContent>
        <Typography className={classes.theme} color="textSecondary" gutterBottom>
          Full Stack developer
        </Typography>
        <Typography className={classes.title} variant="h5" component="h2">
          Parktout
        </Typography>
        <div className = {classes.descriptionContainer}>
        <Typography className={classes.description} variant="body2" component="p">
          An android application that provides information about more than 1000 parkings in Lyon, France.
          I managed a team of 5 members and participated on data pre-processing for parking availability prediction.
        </Typography>
        </div>
      </CardContent>
      <CardActions>
      
      <a href = "https://github.com/felipeangelimvieira/Parktout"> 
        <IconButton className={classes.iconButton} aria-label="Add to favorites">
          <Code/>
        </IconButton>
        </a>

        <a href = "https://youtu.be/yv8BwVhAJ1o"> 
        <IconButton className={classes.iconButton} aria-label="Add to favorites">
          <FeaturedVideo/>
        </IconButton>
        </a>
      	
      </CardActions>
    </Card>

    <Card className={classes.card}>
      <CardContent>
        <Typography className={classes.theme} color="textSecondary" gutterBottom>
          Machine Learning
        </Typography>
        <Typography className={classes.title} variant="h5" component="h2">
          RotationNet - implementation with TensorflowHub
        </Typography>
        <div className = {classes.descriptionContainer}>
        <Typography className={classes.description} variant="body2" component="p">
          A versatile implementation of RotationNet, an image-based neural network
          for 3D object recognition and pose estimation.
        </Typography>
        </div>
      </CardContent>
      <CardActions>
      
      <a href = "https://github.com/felipeangelimvieira/RotationNet-Hub"> 
        <IconButton className={classes.iconButton} aria-label="Add to favorites">
          <Code/>
        </IconButton>
      </a>
        
      </CardActions>
    </Card>



    <Card className={classes.card}>
      <CardContent>
        <Typography className={classes.theme} color="textSecondary" gutterBottom>
          Research
        </Typography>
        <Typography className={classes.title} variant="h5" component="h2">
          How batteries affect electric vehicles autonomy
        </Typography>
        <div className = {classes.descriptionContainer}>
        <Typography className={classes.description} variant="body2" component="p">
          As a student supervised by Silvio de Almeida, I developed a research about
          how different battery specifications affect the electric vehicle's driving
          range. The research was published in one scientific journal and three conferences.
          The project was my first contact with academic research, and it catalyzed my taste for
          scientific papers.
        </Typography>
        </div>
      </CardContent>
      <CardActions>
      
      <a href = "https://revistas.ufpr.br/reterm/article/view/64129"> 
        <IconButton className={classes.iconButton} aria-label="Add to favorites">
          <Description/>
        </IconButton>
      </a>

      <a href = "http://www.sistema.abcm.org.br/articleFiles/download/11063"> 
        <IconButton className={classes.iconButton} aria-label="Add to favorites">
          <Description/>
        </IconButton>
      </a>

      <a href = "http://abcm.org.br/app/webroot/anais/conem/2016/default.htm?query=CON-2016-1661"> 
        <IconButton className={classes.iconButton} aria-label="Add to favorites">
          <Description/>
        </IconButton>
      </a>

      
      
        
      </CardActions>
    </Card>

    </div>
    )
}

